import React from 'react'
// @ts-ignore
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter'
import {
  vscDarkPlus,
  coyWithoutShadows,
  darcula,
  tomorrow,
  // @ts-ignore
} from 'react-syntax-highlighter/dist/esm/styles/prism' // 

// vscDarkPlus vscode 
// darcula  webstorm 
// coyWithoutShadows 

type tProps = {
  textContent: string
  language: string
  darkMode?: boolean
}
// npm install react-syntax-highlighter --save
const them = {
  dark: vscDarkPlus,
  light: tomorrow,
  // light: coyWithoutShadows,
}

const OmsSyntaxHighlight = (props: tProps) => {
  const { textContent, darkMode, language = 'txt' } = props
  if (typeof darkMode === 'undefined') {
    them.light = darcula
  }
  if (typeof darkMode === 'boolean') {
    them.light = coyWithoutShadows
  }
  return (
    <SyntaxHighlighter
      showLineNumbers={true} // 
      lineNumberStyle={{ color: '#ddd', fontSize: 10, lineHeight: '28px' }} // 
      style={darkMode ? them.dark : them.light} // 
      language={language} //  css, jsx , javascript 
      PreTag="div"
    >
      {String(textContent).replace(/\n$/, '')}
    </SyntaxHighlighter>
  )
}

export default OmsSyntaxHighlight
